html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/Ubuntu-Regular.woff2") format("woff2"), url("../fonts/Ubuntu-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Bebas Neue";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Bebas-Neue-bold.woff2") format("woff2"), url("../fonts/Bebas-Neue-bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Montserrat-400.woff2") format("woff2"), url("../fonts/Montserrat-400.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 900;
  src: url("../fonts/Montserrat-900.woff2") format("woff2"), url("../fonts/Montserrat-900.woff") format("woff");
  font-display: swap;
}
.container {
  min-height: 100vh;
  width: 100%;
  max-width: 320px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}
@media screen and (min-width: 1900px) {
  .container {
    max-width: 1920px;
  }
}

.container--voice {
  background-color: #181818;
}
@media screen and (min-width: 1900px) {
  .container--voice {
    background: radial-gradient(circle, #2c2c2c 0, transparent 0) 0 0, radial-gradient(circle, #2c2c2c 2px, transparent 4px) 0 20px;
    background-size: 40px 40px;
    background-color: #181818;
    background-repeat: repeat;
  }
}

.page__body {
  font-family: "Ubuntu", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 170%;
  color: #181818;
  margin: 0;
}
@media screen and (min-width: 1900px) {
  .page__body {
    font-size: 22px;
  }
}

.page__body-voice {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 170%;
  color: #ffffff;
  margin: 0;
}
@media screen and (min-width: 1900px) {
  .page__body-voice {
    font-size: 22px;
  }
}

.table__title {
  margin: 14px 102px 28px 12px;
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-weight: 700;
  font-size: 38px;
  line-height: 100%;
  color: #0b64fe;
  text-transform: uppercase;
}
@media screen and (min-width: 1900px) {
  .table__title {
    font-family: "Ubuntu", "Arial", sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 62px;
    line-height: 100%;
    text-transform: none;
    margin-bottom: 30px;
    max-width: 566px;
    grid-column: 2/-1;
    grid-row: 1/2;
  }
}

.table__content {
  margin: 0 12px;
}
@media screen and (min-width: 1900px) {
  .table__content {
    max-width: 500px;
    grid-column: 2/-1;
  }
  .table__content--material {
    grid-row: 2/3;
  }
  .table__content--properties {
    grid-row: 3/4;
  }
}

.table__tabletop-img {
  position: absolute;
  width: 320px;
  height: 286px;
}
@media screen and (min-width: 1900px) {
  .table__tabletop-img {
    width: 976px;
    height: 782px;
  }
}

.table__legs-img {
  display: block;
  position: relative;
}
@media screen and (min-width: 1900px) {
  .table__legs-img {
    width: 976px;
    height: 782px;
  }
}

.table__picture {
  margin-bottom: 48px;
}
@media screen and (min-width: 1900px) {
  .table__picture {
    grid-row: 1/-1;
    max-width: 976px;
  }
}

@media screen and (min-width: 1900px) {
  .table {
    display: grid;
    grid-template-columns: repeat(2, 50%);
    grid-auto-rows: 252px;
    gap: 30px 0;
  }
}

.table__tabletop-img--static {
  top: 468px;
}
@media screen and (min-width: 1900px) {
  .table__tabletop-img--static {
    top: 0%;
  }
}

.table__tabletop-img--up {
  animation-name: tableup;
  animation-duration: 500ms;
  animation-iteration-count: 1;
  top: 456px;
}

.table__tabletop-img--down {
  animation-name: tabledown;
  animation-duration: 500ms;
  animation-iteration-count: 1;
  top: 480px;
}

.buttons {
  position: absolute;
  width: 144px;
  height: 62px;
  top: 650px;
  left: 50%;
  transform: translate(-54%, -50%);
  display: flex;
  justify-content: space-between;
}
@media screen and (min-width: 1900px) {
  .buttons {
    width: 474px;
    height: 202px;
    top: 510px;
    left: 26%;
  }
}

.button {
  border: none;
  border-radius: 50%;
  background-color: #a6ffff;
  width: 60px;
  height: 60px;
  cursor: pointer;
}
@media screen and (min-width: 1900px) {
  .button {
    width: 200px;
    height: 200px;
    font-size: 40px;
  }
}
.button:hover {
  background-color: #4bfafa;
}
.button:active {
  background-color: #48dada;
  border: 1px dashed #4bfafa;
}

@media screen and (min-width: 1900px) {
  .link {
    grid-row: 4/-1;
  }
}

@media screen and (min-width: 1900px) {
  .phrase {
    grid-row: 3/4;
    grid-column: 1/2;
  }
}

.phrase__to-say {
  font-style: normal;
  font-weight: 900;
  font-size: 39px;
  line-height: 90%;
  margin: 30px 122px 10px 16px;
}
@media screen and (min-width: 1900px) {
  .phrase__to-say {
    margin: 0 0 18px 100px;
    text-transform: uppercase;
  }
}

.phrase__text {
  font-style: normal;
  font-weight: 900;
  font-size: 49px;
  line-height: 90%;
  text-transform: uppercase;
  margin: 0 2px 10px 16px;
}
@media screen and (min-width: 1900px) {
  .phrase__text {
    margin-left: 160px;
  }
}

.voice-control__title {
  font-size: 34px;
  line-height: 34px;
  font-weight: 400;
  margin: 16px 76px 0 16px;
}
@media screen and (min-width: 1900px) {
  .voice-control__title {
    grid-row: 1/2;
    grid-column: 1/2;
    margin: 72px 100px 60px 100px;
    font-size: 56px;
    line-height: 72px;
    max-width: 408px;
  }
}

.voice-control__content {
  margin: 0 16px 30px;
}

.voice-control__lady-img {
  z-index: 20;
  display: block;
  position: absolute;
  margin: 0 auto;
  background-color: transparent;
  background-image: url("../img/voice/lady.png");
  width: 290px;
  height: 756px;
  background-repeat: no-repeat;
  left: 50%;
  transform: translateX(-50%);
  bottom: 3%;
  animation-name: ladymob, livitationmob;
  animation-duration: 6s, 3s;
  animation-iteration-count: 1, infinite;
  animation-delay: 0s, 6s;
}
@media screen and (min-width: 1900px) {
  .voice-control__lady-img {
    grid-row: 2/4;
    grid-column: 2/3;
    position: relative;
    bottom: -3%;
    transform: translateX(-60%);
    animation-name: lady, livitation;
    animation-duration: 6s, 3s;
    animation-iteration-count: 1, infinite;
    animation-delay: 0s, 6s;
  }
}

.voice-control__hole {
  width: 200px;
  height: 60px;
  margin: 0 auto;
  background-color: #000000;
  border-radius: 50%;
  z-index: 10;
  animation-name: hole;
  animation-duration: 6s;
  animation-iteration-count: 1;
}
@media screen and (min-width: 1900px) {
  .voice-control__hole {
    order: 3;
    grid-row: 3/4;
    grid-column: 2/3;
  }
}

.voice-control__img-wrapper {
  margin-top: 50px;
  position: relative;
  height: 750px;
  display: flex;
  align-items: flex-end;
}
@media screen and (min-width: 1900px) {
  .voice-control__img-wrapper {
    height: 760px;
    display: block;
  }
}

.voice-control__img {
  margin-top: 30px;
}
@media screen and (min-width: 1900px) {
  .voice-control__img {
    grid-row: 1/4;
    grid-column: 3/4;
    width: 842px;
    height: 868px;
  }
}

@media screen and (min-width: 1900px) {
  .voice-control {
    display: grid;
    grid-template-columns: 2fr 1fr 2fr;
    grid-auto-rows: 250px 500px 250px;
  }
}

@media screen and (min-width: 1900px) {
  .voice-control__text-wrapper {
    grid-row: 2/4;
    grid-column: 1/2;
    width: 586px;
    height: 100%;
    margin: 30px 0 0 72px;
  }
}

@keyframes hole {
  0% {
    width: 100px;
  }
  50% {
    width: 300px;
  }
  100% {
    width: 200px;
  }
}
@keyframes ladymob {
  0% {
    bottom: -750px;
  }
  50% {
    bottom: -410px;
  }
  100% {
    bottom: 0;
  }
}
@keyframes lady {
  0% {
    bottom: -820px;
  }
  50% {
    bottom: -410px;
  }
  100% {
    bottom: -15px;
  }
}
@keyframes livitationmob {
  0% {
    bottom: 2%;
  }
  50% {
    bottom: 5%;
  }
  100% {
    bottom: 2%;
  }
}
@keyframes livitation {
  0% {
    bottom: -3%;
  }
  50% {
    bottom: -6%;
  }
  100% {
    bottom: -3%;
  }
}
@keyframes tableup {
  0% {
    top: 468px;
  }
  100% {
    top: 456px;
  }
}
@keyframes tabledown {
  0% {
    top: 468px;
  }
  100% {
    top: 480px;
  }
}